
@font-face {
  font-family: "Neutral";
  src: url("/assets/fonts/NeutralBP.4cff5c9.eot?#iefix") format("embedded-opentype"), url("/assets/fonts/NeutralBP.4cff5c9.otf") format("opentype"), url("/assets/fonts/NeutralBP.4cff5c9.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "LarishNeueSemibold-Regular";
  src: url("/assets/fonts/LarishNeueSemibold-Regular.eot?#iefix") format("embedded-opentype"), url("/assets/fonts/LarishNeueSemibold-Regular.otf") format("opentype"), url("/assets/fonts/LarishNeueSemibold-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
html {
  font-size: 20px;
}
body {
  font-family: "Neutral", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #2c3e50;
  padding: 2%;
}
img {
  max-width: 100%;
  height: auto;
}
h1 {
  font-family: "Larish-Neue", serif;
}
nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 80px;
}
nav :nth-of-type(1) {
    -webkit-box-flex: 3;
        -ms-flex-positive: 3;
            flex-grow: 3;
}
nav a {
    font-size: 1rem;
    margin-right: 20px;
}
.lh-menu {
  height: 100vh;
  width: 100vw;
  background: red;
  position: absolute;
  top: 70px;
  left: 0;
  z-index: 10;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 80px;
}
.lh-menu :nth-of-type(1) {
    -webkit-box-flex: 3;
        -ms-flex-positive: 3;
            flex-grow: 3;
}
.lh-menu a {
    font-size: 1rem;
    margin-right: 20px;
}
.lh-controller {
  z-index: 100;
}

.fade-enter-active[data-v-5e85ea10],
.fade-leave-active[data-v-5e85ea10] {
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
.fade-enter[data-v-5e85ea10],
.fade-leave-to[data-v-5e85ea10] {
  opacity: 0;
}
